var asin = Math.asin;
var cos = Math.cos;
var sin = Math.sin;
var sqrt = Math.sqrt;
var PI = Math.PI; // equatorial mean radius of Earth (in meters)

var R = 6378137;

function squared(x) {
  return x * x;
}

function toRad(x) {
  return x * PI / 180.0;
}

function hav(x) {
  return squared(sin(x / 2));
} // hav(theta) = hav(bLat - aLat) + cos(aLat) * cos(bLat) * hav(bLon - aLon)


function haversineDistance(a, b) {
  var aLat = toRad(Array.isArray(a) ? a[1] : a.latitude || a.lat);
  var bLat = toRad(Array.isArray(b) ? b[1] : b.latitude || b.lat);
  var aLng = toRad(Array.isArray(a) ? a[0] : a.longitude || a.lng || a.lon);
  var bLng = toRad(Array.isArray(b) ? b[0] : b.longitude || b.lng || b.lon);
  var ht = hav(bLat - aLat) + cos(aLat) * cos(bLat) * hav(bLng - aLng);
  return 2 * R * asin(sqrt(ht));
}

module.exports = haversineDistance;